/* @flow */

import React from 'react';
import styled from 'styled-components';

const Container = styled.div`
  background: #b99459;
  width: 100%;
  margin-bottom: 10px;
  padding: 10px;
`;

const Title = styled.h3`
  margin: 0;
  margin-bottom: 10px;
  color: #362c2a;
`;

const ProgressBg = styled.div`
  width: calc(100% - 20px);
  height: 25px;
  background: #dab272;
  box-sizing: border-box;
`;

const ProgressInner = styled.div`
  height: 100%;
  width: 0%;
  background: #8a6326;
  transition: 1s all;
`;

type Props = {
};

class HealingLevel extends React.Component<Props> {
  render() {
    return (
      <Container>
        <Title>{this.props.title}</Title>
        <ProgressBg>
          <ProgressInner style={{width: `${this.props.percentComplete * 100}%`, backgroundColor: this.props.color}}/>
        </ProgressBg>
      </Container>
    );
  }
}

export default HealingLevel;



