/* @flow */

import React from 'react';
import styled from 'styled-components';
import HealingLevel from './HealingLevel';
import {ContentSecCTA} from '../common';

const Container = styled.div`
  styles
`;

const TICK_MS = 1000;

type Props = {};

class ReikiHealingV1 extends React.Component<Props> {
  state = {
    currentHealingLevel: 0,
    healingLevelsData: [
      {
        title: `Full body healing`,
        timeInS: 10 * 60,
        color: `#03cfff`,
        timeElapsedInMs: 0,
        state: `todo`,
        percentComplete: 0,
      },
      // {
      //   title: `Distance healing synchronization`,
      //   timeInS: 5,
      //   color: `#03cfff`,
      //   timeElapsedInMs: 0,
      //   state: `todo`,
      //   percentComplete: 0,
      // },
      {
        title: `Toes healing`,
        timeInS: 15,
        color: `#03cfff`,
        timeElapsedInMs: 0,
        state: `todo`,
        percentComplete: 0,
      },
      {
        title: `Finger healing`,
        timeInS: 30,
        color: `#03cfff`,
        timeElapsedInMs: 0,
        state: `todo`,
        percentComplete: 0,
      },
      {
        title: `Left arm healing`,
        timeInS: 50,
        color: `#03cfff`,
        timeElapsedInMs: 0,
        state: `todo`,
        percentComplete: 0,
      },
      {
        title: `Right arm healing`,
        timeInS: 50,
        color: `#03cfff`,
        timeElapsedInMs: 0,
        state: `todo`,
        percentComplete: 0,
      }
    ]
  };

  componentDidMount() {
    setInterval(this.tick, TICK_MS)
  }

  render() {
    return (
      <Container>
        <ContentSecCTA onClick={this.startHealing}>Start healing</ContentSecCTA>
        <div>
          {
            this.state.healingLevelsData.map((healingLevelData, healingLevelCompleted) => {
              return <HealingLevel key={`${healingLevelCompleted}-level`} {...healingLevelData}/>
            })
          }
        </div>
      </Container>
    );
  }

  startHealing = () => {
    const {currentHealingLevel} = this.state;
    const healingLevelsData = this.state.healingLevelsData.map((healingLevelData, i) => {
      if(currentHealingLevel === i){
        healingLevelData = {
          ...healingLevelData,
          state: `running`,
        }
      }
      return healingLevelData;
    });
    this.setState({healingLevelsData});
  };

  tick = () => {
    const healingLevelsData = this.state.healingLevelsData.map(healingLevelData => {
      if (healingLevelData.state === `running`) {
        const timeElapsedInMs = healingLevelData.timeElapsedInMs + TICK_MS;
        let percentComplete = timeElapsedInMs / (healingLevelData.timeInS * 1000);
        let state = healingLevelData.state;
        if (percentComplete > 1) {
          percentComplete = 1;
          state = `done`;
        }
        healingLevelData = {
          ...healingLevelData,
          timeElapsedInMs,
          percentComplete,
          state,
        }
      }
      return healingLevelData;
    });
    this.setState({healingLevelsData});
  };

  onHealingLevelComplete = (healingLevelCompleted) => {

  }
}

export default ReikiHealingV1;



