/* @flow */

import React from 'react';
import styled from 'styled-components';
import Link from '../Link';
import buttonImage from './btn_donateCC_LG.gif';

const StyledLink = styled(Link)`
  text-align: center;
  display: inherit;
  background: #fffcd9;
  padding: 16px;
  margin-bottom: 10px;
  border-radius: 15px;
`;

type Props = {};

class PayPalDonate extends React.Component<Props> {
  render() {
    return (
      <StyledLink href="https://www.paypal.me/FreeReikiCourse/4.99">
        <img src={buttonImage}/>
      </StyledLink>
    );
  }
}

export default PayPalDonate;



